.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.3);
  display: flex;
  justify-content: center;
  padding-top: 5rem;
}

.modal-body {
  width: 400px;
  height: 350px;
  background: black;
  border: solid;
  border-radius: 3rem;
  padding: 2rem;
}

.auth_error {
  font-size: 14pt;
  color: rgb(218, 16, 5);
}

.text_boxes {
  width: 100%;
  height: 30px;
  text-align: center;
  font-size: 12pt;
  font-family: "Georgia", serif;
  color: #42100d;
  background: white;
  border-radius: 0.5rem;
  margin-bottom: 30px;
}

.buttonsDiv {
  text-align: center;
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
}

.loginButton {
  width: 150px;
  height: 70px;
  font-size: 15pt;
  color: wheat;
  background-color: black;
  border: solid;
  border-radius: 30%;
}

.loginButton:hover,
.buttons:hover {
  color: rgb(137, 207, 207);
  background-color: rgb(24, 24, 24);
}

.buttons {
  width: 150px;
  font-size: 15pt;
  color: wheat;
  background-color: black;
  border: solid;
  border-radius: 0.5rem;
}
